  <div class="login-wrap">
    <a-form :model="loginForm" class="login-container">
      <h3 class="title">用户登录</h3>
      <a-form-item>
        <a-input type="text" v-model:value="loginForm.username" placeholder="账号"></a-input>
      </a-form-item>
      <a-form-item>
        <a-input type="password" v-model:value="loginForm.password" auto-complete="off" placeholder="密码"></a-input>
      </a-form-item>
      <a-checkbox-group v-model:value="loginForm.rememberMe">
        <a-checkbox class="remember" value="on">记住我</a-checkbox>
      </a-checkbox-group>
      <a-form-item style="width:100%;">
        <a-button type="primary" style="width:100%;" @click="submitForm" :loading="logining">登录</a-button>
<!--        <br/><a-button type="primary" style="width:100%;" @click="checkAuthor" :loading="check">检查auth</a-button>-->
      </a-form-item>
    </a-form>
  </div>
