export default {
  data(){
    return {
      loginForm: reactive({
        username: '',
        password: '',
        rememberMe: []
      }),
      logining: ref(false)
    };
  },
  methods: {
    submitForm(){
      // console.log(this.loginForm);
      // console.log(this.$store.state.config);
      // let loginUrl = 'http://' + this.$store.state.config.domain + '/api/session';
      this.logining = true;
      let loginUrl = '/api/session';
      if(this.loginForm.rememberMe.length > 0){
        loginUrl += '?remember-me=on';
      }
      axios.post(loginUrl, this.loginForm).then(res=>{
        this.logining = false;
        if(res.data.status === 200){
          localStorage.setItem('userInfo', JSON.stringify(res.data.userInfo));
          this.$router.push('/index');
        }
      });
    },
    checkAuthor(){
      axios.get('/api/admin/hello').then(res=>{
        console.log(res.data);
      });
    }
  }
}
